.circles{
    width: 300px;
    height: 300px;
    position: relative;
    perspective: 50000px;
    color: #5ed3f3;
    z-index: 0;
}

.circles span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display:block;
    background: currentColor;
    width: 18.4%;
    height: 18.4%;
    border-radius:50%;
}

.circles div{
    position: absolute;
    width: 100%;
    height: 100%;
    border-left: 13.32px solid;
    border-right: 5.123076px solid;
    border-top: 13.32 solid transparent;
    border-radius: 100%;
}

.circles div:nth-child(1){
    animation: anim-1 3s linear infinite;
}
.circles div:nth-child(2){
    animation: anim-2 3s linear infinite;
}
.circles div:nth-child(3){
    animation: anim-3 3s linear infinite;
}

@keyframes anim-1{
    from{
        transform: rotateZ(120deg) rotateX(66deg) rotateZ(0);
    }
    to {
        transform: rotateZ(120deg) rotateX(66deg) rotateZ(360deg);
    }
}
@keyframes anim-2{
    from{
        transform: rotateZ(240deg) rotateX(66deg) rotateZ(0);
    }
    to {
        transform: rotateZ(240deg) rotateX(66deg) rotateZ(360deg);
    }
}
@keyframes anim-3{
    from{
        transform: rotateZ(360deg) rotateX(66deg) rotateZ(0);
    }
    to {
        transform: rotateZ(360deg) rotateX(66deg) rotateZ(360deg);
    }
}

@media screen and (max-width: 1050px) {
    .circles{
        width: 300px;
        height: 300px;
    }
}